function navigator(url){
	uni.navigateTo({
		url:url,
		animationType: 'pop-in',
		animationDuration: 300
	})
}

function call(res){
	uni.makePhoneCall({
				phoneNumber:res, //
				success: function(e) {
					console.log(e);
				},
				fail: function(e) {
					console.log(e);
				}
			})
}

function transitTime(timestamp) {
  let date = new Date(timestamp * 1000); //10*1000131000
  let Y = date.getFullYear() + '';
  let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '';

  function change(stage) {
    if (stage < 10) {
      return "0" + stage;
    } else {
      return stage;
    }
  }
  let D = change(date.getDate()) + ' ';
  let h = change(date.getHours()) + ':';  // 
  let m = change(date.getMinutes());      // 
  let s = change(date.getSeconds());      // 
  return Y + M + D + h + m;
}

